
import ImgUpload from "./imgUpload.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
name: 'AddImg',
data () {
return {
activeList: []
}
},
components: {
ImgUpload,
XTextarea
},
props: {
type: {
type: String,
default: ''
}
},
methods: {
blurq(e){
var scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
document.documentElement.scrollTop = scrollTop;
},
getUrl(url, item){
item.image = url
item.html = "<img src='" + url + "' style='width: 100%; height: 100%;'/>"
},
addActive(type){
this.activeList.push({
type: type,
image: '',
html: ''
})
},
deleteThis(index){
this.activeList.splice(index, 1)
},
upData (index) {
this.activeList = this.swapItems(this.activeList, index, index - 1)
},
downData (index) {
this.activeList = this.swapItems(this.activeList, index, index + 1)
},
swapItems: function(arr, index1, index2){
arr[index1] = arr.splice(index2,1,arr[index1])[0]
return arr
}
}
}



