
import Selector from 'vux/src/components/selector/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Clipboard from "clipboard";
import CountDown from "./count-down.vue";
import Data from "./data.js";
import Scroll from "./scroll.vue";
var baseUrl = require("../../config");
export default {
name: "info",
components: {
Selector,
XDialog,
Alert,
Loading,
Group,
Cell,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
Scroll,
CountDown
},
data() {
return {
headimgurl: "",
userName: "",
shown: false,
pshown: false,
alertShow: false,
alertContent: "",
loading: false,
tousuDialog: false,
supportDialog: false,
appDialog: false,
home: true,
active: "pay",
users: [],
user_id: "",
params: {},
orderList: [],
packageList: [],
order: [],
formD: Data,
isLoading: true,
name: "",
tel: "",
type: "",
options: [
{
key: 0,
value: "新会（学）员"
},
{
key: 1,
value: "旧会（学）员"
}
],
musicUrl: ""
};
},
props: {
form: {
type: Object
}
},
mounted() {
this.query();
},
methods: {
close() {
this.supportDialog = false;
this.appDialog = false;
},
support() {
this.supportDialog = true;
},
goapp() {
this.appDialog = true;
},
phone() {
this.pshown = true;
},
showntousu() {
this.tousuDialog = true;
},
tousu() {
this.$vux.toast.text("投诉成功！", "top");
this.tousuDialog = false;
},
copy() {
var clipboard = new Clipboard("#copyBtn");
clipboard.on("success", e => {
this.$vux.toast.text("复制成功！", "top");
e.clearSelection();
});
},
show() {
this.active = "pay";
this.shown = true;
},
showdialoge() {
this.name = "";
this.type = "";
this.tel = "";
},
hidedailog() {
this.shown = false;
this.pshown = false;
},
goback() {
this.home = true;
this.active = "pay";
},
getorder() {
this.active = "order";
this.home = false;
this.loading = true;
this.$http
.post(baseUrl.apiBaseUrl + "api/order/getOrderByUserIdAndActivityId", {
activityId: this.params.id,
user_id: this.user_id || ""
})
.then(({ data: res }) => {
this.orderList = res.result.data;
this.loading = false;
});
},
package() {
this.active = "package";
this.loading = true;
this.$http
.post(baseUrl.apiBaseUrl + "api/order/getOrderByFromUserId ", {
activityId: this.params.id,
from_user: this.params.userid || ""
})
.then(({ data: res }) => {
this.loading = false;
this.packageList = res.result.data;
});
},
randomNum(minNum, maxNum) {
switch (arguments.length) {
case 1:
return parseInt(Math.random() * minNum + 1, 10);
break;
case 2:
return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
break;
default:
return 0;
break;
}
},
pay() {
let max = this.formD.maxValue;
let min = this.formD.minValue;
if (!this.name || this.type === "" || !this.tel) {

return this.$vux.toast.text("请填写姓名，电话，类别", "top");
}

this.$vux.toast.text("报名成功");
wx.ready(() => {
var shareParam = {
title: `我是${this.name || this.userName}, 参加了${
this.formD.activityTheme
}`, // 分享标题
desc: `${this.formD.activityTheme}, 联系电话: ${this.tel}`, // 分享描述
link:
baseUrl.apiBaseUrl +
"dist/redirect.html?id=" +
this.params.id +
"&userid=" +
this.user_id +
"&hash=Info",

imgUrl: thumbnail, // 分享图标


trigger: function(res) {
console.log("用户点击发送给朋友");
},
success: function(res) {
console.log("已分享");
},
cancel: function(res) {
console.log("已取消");
},
fail: function(res) {}
};

wx.onMenuShareTimeline(shareParam);
wx.onMenuShareAppMessage(shareParam);
});
this.shown = false;
this.$http
.post(baseUrl.apiBaseUrl + "api/order/save", {
activityId: this.params.id,
from_user: this.params.userid || "",
red_packets: this.randomNum(min, max),
total_price: this.formD.productPrice,
user_name: this.name,
user_type: this.type,
mobile: this.tel,
user_id: this.user_id
})
.then(({ data: res }) => {
console.log(res);
if (res.code != "0000") {
return;
} else {
this.$http
.post(
baseUrl.apiBaseUrl +
"api/wxpay/prepay?user_id=" +
this.user_id +
"&total_fee=" +
this.formD.productPrice.toFixed(2) +
"&orderId=" +
res.result.orderId,
{}
)
.then(({ data: res }) => {
console.log(res);
WeixinJSBridge.invoke(
"getBrandWCPayRequest",
{
appId: "wx2517d7a8920ab213",
timeStamp: res.result.timestamp,

nonceStr: res.result.nonceStr,

package: res.result.package,

signType: "MD5",

paySign: res.result.paySign
},
res => {

if (res.err_msg == "get_brand_wcpay_request:ok") {
this.$vux.toast.text("支付成功！", "top");
} else {
this.$vux.toast.text("支付失败！", "top");
}
}
);
});
}
});
},
stop() {
var audio = document.getElementById("audio2");
if (audio == null) return;
audio.pause();
},
play(item) {
var audio = document.getElementById("audio2");
if (audio == null) return;
if (this.formD.autoPlayMusic) {
let data = JSON.parse(this.formD.music);
audio.src = data.path;
audio.pause();
audio.load();
audio.play();
}
},

query() {
if (!!this.form) {
this.formD = this.form;
this.play();
return;
}
var params = this.$route.query,
user_id;

this.params = params;

if (!!params.code) {
this.$http
.get(
baseUrl.apiBaseUrl +
"api/wxpay/getUserInfo?code=" +
params.code +
"&state=" +
params.state
)
.then(({ data: res }) => {
if (res.code != "0000") {
console.log("获取用户信息失败");
} else {
user_id = res.result.data.user.userId;
this.userName = res.result.data.user.username;
this.headimgurl = res.result.data.user.headimgurl;
this.user_id = user_id;
this.loading = true;
this.$http
.get(baseUrl.apiBaseUrl + "distribution/info/" + params.id)
.then(({ data }) => {
if (data.code == "0") {
this.formD = data.distribution;
var fore = data.distribution;
document.title = fore.activityTheme;
this.users = data.user;
this.order = data.order;
this.formD.music = !!this.formD.music
? JSON.parse(this.formD.music)
: "";
var thumbnail = data.distribution.thumbnail;
var currentUrl = encodeURIComponent(
location.href.split("#")[0]
);


console.log(currentUrl);

this.$http
.post(
baseUrl.apiBaseUrl +
"api/wxpay/initwxjs?url=" +
currentUrl
)
.then((data, status) => {
this.loading = false;
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: data.data.result.data.appId, // 必填，公众号的唯一标识
timestamp: data.data.result.data.timestamp, // 必填，生成签名的时间戳
nonceStr: data.data.result.data.nonceStr, // 必填，生成签名的随机串
signature: data.data.result.data.signature, // 必填，签名s
jsApiList: [
"onMenuShareTimeline",
"onMenuShareAppMessage",
"chooseWXPay"
] // 必填，需要使用的JS接口列表
});

wx.ready(() => {


wx.error(function(res) {

console.log(res);
});

wx.checkJsApi({
jsApiList: [
"onMenuShareTimeline",
"onMenuShareAppMessage",
"chooseWXPay"
], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function(res) {



}
});
var shareParam = {
title: `我是${this.userName}, 参加了${
fore.activityTheme
}`, // 分享标题
desc: `${fore.activityTheme}, 联系电话: ${
fore.phone
}`, // 分享描述
link:
baseUrl.apiBaseUrl +
"dist/redirect.html?id=" +
params.id +
"&userid=" +
this.user_id +
"&hash=Info",

imgUrl: thumbnail, // 分享图标


trigger: function(res) {
console.log("用户点击发送给朋友");
},
success: function(res) {
console.log("已分享");
},
cancel: function(res) {
console.log("已取消");
},
fail: function(res) {}
};

wx.onMenuShareTimeline(shareParam);
wx.onMenuShareAppMessage(shareParam);
});
});
}
});
this.$http
.post(baseUrl.apiBaseUrl + "distribution/addWatcher", {
id: params.id,
watcher: res.result.data.user.userId
})
.then(({ data: res }) => {
console.log("添加成功信息：", res);
});
}
});
}
}
}
};



