
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Range from 'vux/src/components/range/index.vue'

import AddImg from "./addImg.vue";
import ImgUpload from "./imgUpload.vue";
import Info from "./info.vue";
import Data from "./data.js";
var baseUrl = require("../../config");
export default {
name: "content-t",
components: {
Group,
Cell,
CellBox,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
ImgUpload,
Info,
Range,
AddImg
},
data() {
return {
showClear: false,
formD: Data,
isLoading: true,
show: true,
musicUrl: "",
image: "",
headImage: "",
ispreview: false,
activeList: [],
musicList: [
{
label: "天空之城",
status: 0,
active: 0,
value: "http://www.ytmp3.cn/down/53430.mp3"
},
{
label: "天空之城2",
status: 0,
active: 0,
value: "http://www.ytmp3.cn/down/52790.mp3"
}
]
};
},
computed: {
_focusOnPop: {
get() {
return this.formD.focusOnPop == 1 ? true : false;
},
set(val) {
this.formD.focusOnPop = val ? 1 : 0;
}
},
_focusOnRed: {
get() {
return this.formD.focusOnRed == 1 ? true : false;
},
set(val) {
this.formD.focusOnRed = val ? 1 : 0;
}
},
_shareRedEnvelopes: {
get() {
return this.formD.shareRedEnvelopes == 1 ? true : false;
},
set(val) {
this.formD.shareRedEnvelopes = val ? 1 : 0;
}
},
_noPaymentNum: {
get() {
return this.formD.noPaymentNum == 1 ? true : false;
},
set(val) {
this.formD.noPaymentNum = val ? 1 : 0;
}
},
_autoPlayMusic: {
get() {
return this.formD.autoPlayMusic == 1 ? true : false;
},
set(val) {
this.formD.autoPlayMusic = val ? 1 : 0;
}
}
},
mounted() {
var params = this.$route.query;
const url = baseUrl.apiBaseUrl + "distribution/info/" + params.id;
this.$http.get(url).then(({ data }) => {
if (data.code == "0") {
this.formD = data.distribution;
if (data.distribution.activityRules != null) {
var dat = JSON.parse(data.distribution.activityRules);
var sc = [];

dat.forEach(e => {
var s =
"<img src='" + e.img + "' style='width: 100%; height: 100%;'/>";
sc.push({
type: e.type,
image: e.img,
html: s
});
});

this.$refs["activityRules"].activeList = sc;
}
if (data.distribution.thumbnail != null) {
this.image =
"<img src='" +
data.distribution.thumbnail +
"' style='width: 100%; height: 100%;'/>";
}
if (
data.distribution.headImage != null ||
data.distribution.headImage != ""
) {
this.headImage =
"<img src='" +
data.distribution.headImage +
"' style='width: 100%; height: 100%;'/>";
}
if (data.distribution.activityDestription != null) {
var dat = JSON.parse(data.distribution.activityDestription);
var sc = [];

dat.forEach(e => {
var s =
"<img src='" + e.img + "' style='width: 100%; height: 100%;'/>";
sc.push({
type: e.type,
image: e.img,
html: s
});
});

this.$refs["activityDestription"].activeList = sc;
}
if (data.distribution.redRules != null) {
var dat = JSON.parse(data.distribution.redRules);
var sc = [];

dat.forEach(e => {
var s =
"<img src='" + e.img + "' style='width: 100%; height: 100%;'/>";
sc.push({
type: e.type,
image: e.img,
html: s
});
});

this.$refs["redRules"].activeList = sc;
}
}
});
},
methods: {
deleteImg() {
this.image = null;
this.formD.thumbnail = null;
},
deleteThis() {
this.headImage = null;
this.formD.headImage = null;
},
getUrl(url) {
this.formD.thumbnail = url;
this.image = "<img src='" + url + "' style='width: 100%;height: 100%;'/>";


},
getHeadUrl(url) {
this.formD.headImage = url;
this.headImage =
"<img src='" + url + "' style='width: 100%;height: 100%;'/>";


},
editpage() {
this.ispreview = !this.ispreview;
},
preview() {
var activityRules = [],
activityDestription = [],
red_rules = [];
this.$refs["activityRules"].activeList.forEach(item => {
activityRules.push({
type: item.type,
img: item.image
});
});
this.$refs["activityDestription"].activeList.forEach(item => {
activityDestription.push({
type: item.type,
img: item.image
});
});
this.$refs["redRules"].activeList.forEach(item => {
red_rules.push({
type: item.type,
img: item.image
});
});

this.formD.activityRules = JSON.stringify(activityRules);
this.formD.activityDestription = JSON.stringify(activityDestription);
this.formD.redRules = JSON.stringify(red_rules);
this.formD.music = JSON.stringify({
path: this.musicUrl
});

var a = JSON.parse(JSON.stringify(this.formD));

this.ispreview = !this.ispreview;
},
save() {
var activityRules = [],
activityDestription = [],
red_rules = [];
this.$refs["activityRules"].activeList.forEach(item => {
activityRules.push({
type: item.type,
img: item.image
});
});
this.$refs["activityDestription"].activeList.forEach(item => {
activityDestription.push({
type: item.type,
img: item.image
});
});

this.$refs["redRules"].activeList.forEach(item => {
red_rules.push({
type: item.type,
img: item.image
});
});

this.formD.activityRules = JSON.stringify(activityRules);
this.formD.activityDestription = JSON.stringify(activityDestription);
this.formD.redRules = JSON.stringify(red_rules);
this.formD.music = this.musicUrl;
var a = JSON.parse(JSON.stringify(this.formD));
this.$vux.loading.show({
text: "Loading"
});
this.$http
.post(baseUrl.apiBaseUrl + "distribution/save", a)
.then(({ data }) => {
this.$vux.loading.hide();
if (data.code == "0") {
this.formD.id = data.distribution.id;
this.$vux.alert.show({
title: "提示",
content: "保存成功！",
onShow() {},
onHide() {}
});
} else {
this.$vux.alert.show({
title: "提示",
content: "保存失败！",
onShow() {},
onHide() {}
});
}
});
},
cancel() {
this.stop();
this.show = !this.show;
},
musicClick() {
this.show = !this.show;
if (!this.show) {
this.musicList.forEach(el => {
if (el.value == this.musicUrl) {
el.active = 1;
} else {
el.active = 0;
}
});
}
},
play(item) {
var audio = document.getElementById("audio");
audio.src = item.value;
audio.loop = true; 
this.musicList.forEach(el => {
el.status = 0;
});
item.status = 1;
audio.pause();
audio.load();
audio.play();
},
stop() {
var audio = document.getElementById("audio");
audio.pause();
audio.load();
this.musicList.forEach(el => {
el.status = 0;
});
},
tab(item) {
this.musicList.forEach(el => {
el.active = 0;
});
item.active = 1;
},
sure() {
this.stop();
var item;
this.musicList.forEach(el => {
if (el.active == 1) {
item = el;
}
});
if (!!item) {
var audio = document.getElementById("audio");
audio.src = item.value;
this.$vux.toast.show({
type: "success",
text: "选择成功！",
position: "middle"
});
this.musicUrl = item.value;
this.show = !this.show;
} else {
this.$vux.toast.show({
type: "warn",
text: "请选择一条",
position: "middle"
});
}
}
}
};



