
import Selector from 'vux/src/components/selector/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Clipboard from "clipboard";
import CountDown from "./count-down.vue";
import ContentWrap from "./content-wrap.vue";
import Data from "./page3Data.js";
import Scroll from "./scroll.vue";
var baseUrl = require("../../config");

export default {
name: "page3-info",
components: {
Selector,
XDialog,
Alert,
Loading,
Group,
Cell,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
Scroll,
ContentWrap,
CountDown
},
data() {
return {
userName: "",
prize: null,
address: "",
shown: false,
pshown: false,
alertShow: false,
alertContent: "",
loading: false,
tousuDialog: false,
supportDialog: false,
appDialog: false,
home: true,
ispreview: false,
users: [],
user_id: "",
b_userId: "",
shareId: "",
params: {},
orderList: [],
packageList: [],
barginLogList: [],
order: [],
formD: Data,
isLoading: true,
name: "",
tel: "",
type: "",
options: [
{
key: 0,
value: "新会（学）员"
},
{
key: 1,
value: "旧会（学）员"
}
],
list: [],
musicUrl: ""
};
},
props: {
form: {
type: Object
}
},
filters: {
filterusername(userName) {
if (typeof userName == "string") {
return userName.replace(
/^(.{1})(?:[\u4e00-\u9fa5, \w]+)(.{1})$/,
"$1*$2"
);
} else {
return userName;
}
}
},
mounted() {
this.query();
},
activated() {














},
methods: {
linkreload() {
location.href =
location.origin +
"/dist/redirect.html" +
"?id=" +
this.params.id +
"&hash=3pageInfo&shown=1";
},
desensitization(str, beginLen, endLen) {
var len = str.length;
var firstStr = str.substr(0, beginLen);
var lastStr = str.substr(endLen);
var middleStr = str
.substring(beginLen, len - Math.abs(endLen))
.replace(/[\s\S]/gi, "*");

tempStr = firstStr + middleStr + lastStr;

return tempStr;
},
initQQMap() {
console.log({
latitude: this.formD.latitude, // 纬度，浮点数，范围为90 ~ -90
longitude: this.formD.longitude, // 经度，浮点数，范围为180 ~ -180。
name: "活动地点", // 位置名
address: this.address, // 地址详情说明
scale: 10 
});
wx.ready(() => {
wx.openLocation({
latitude: this.formD.latitude, // 纬度，浮点数，范围为90 ~ -90
longitude: this.formD.longitude, // 经度，浮点数，范围为180 ~ -180。
name: "活动地点", // 位置名
address: this.address, // 地址详情说明
scale: 10, // 地图缩放级别,整形值,范围从1~28。默认为最大
infoUrl: `http://apis.map.qq.com/uri/v1/marker?marker=coord:${
this.formD.latitude
},${this.formD.longitude};title:活动地点;addr:${this.address}` // 在查看位置界面底部显示的超链接,可点击跳转
});
});
},
addActive() {
if (this.ispreview) {
return;
}
if (!this.name || !this.tel) {

return this.$vux.toast.text("请填写姓名，电话", "top");
}
this.$http
.post(baseUrl.apiBaseUrl + "api/order/saveOrder", {
activityId: this.params.id,
from_user: "",
red_packets: "",
total_price: this.formD.originalPrice,
user_name: this.name,
orderType: "3",
user_type: "",
mobile: this.tel,
user_id: this.user_id
})
.then(({ data: res }) => {
if (res.code == "0000") {
this.$vux.alert.show({
title: "提示",
content: "参加成功！分享好友帮砍价，赢礼品。"
});
this.shareId = res.result.order.orderId;
this.shown = false;
wx.ready(() => {
var shareParam = {
title: `我是${this.name || this.userName}, 参加了${
this.formD.activityName
}`, // 分享标题
desc: `${this.formD.activityName}, 联系电话: ${this.tel}`, // 分享描述
link:
baseUrl.apiBaseUrl +
"dist/redirect.html?id=" +
this.params.id +
"&userid=" +
this.user_id +
"&shareId=" +
this.shareId +
"&hash=3pageInfo", // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
imgUrl: this.formD.thumbnail, // 分享图标


trigger: function(res) {
console.log("用户点击发送给朋友");
},
success: function(res) {
console.log("已分享");
},
cancel: function(res) {
console.log("已取消");
},
fail: function(res) {}
};
wx.onMenuShareTimeline(shareParam);
wx.onMenuShareAppMessage(shareParam);
});
this.$http
.post(baseUrl.apiBaseUrl + "bargin/queryBarginLog", {
orderId: this.shareId
})
.then(({ data }) => {
if (data.code == "0000") {
this.barginLogList = data.result.data;
}
});
this.$http
.post(baseUrl.apiBaseUrl + "api/order/getOrderByOrderId", {
orderId: res.result.order.orderId
})
.then(({ data: res1 }) => {
if (res1.code == "0000") {
this.prize = res1.result.data.total_price;
this.b_userId = res1.result.data.user_id;
}
});
} else {
this.$vux.alert.show({
title: "提示",
content: res.msg
});
}
});
},
addZan() {
if (this.ispreview) {
return;
}
if (!this.shareId) {

return this.$vux.toast.text("请参加活动！");
}
this.$http
.post(baseUrl.apiBaseUrl + "bargin/bargin", {
activityId: this.params.id,
orderId: this.shareId,
total_price: this.prize,
user_id: this.user_id
})
.then(({ data }) => {
if (data.code == 0) {
this.$vux.alert.show({
title: "提示",
content: `恭喜你成功砍价${data.result.data.total_price}元`
});
this.$http
.post(baseUrl.apiBaseUrl + "api/order/getOrderByOrderId", {
orderId: this.shareId
})
.then(({ data }) => {
if (data.code == "0000") {
this.prize = data.result.data.total_price;
this.b_userId = data.result.data.user_id;
}
});
} else {
this.$vux.alert.show({
title: "提示",
content: data.msg
});
}
});
},
linkPay() {
if (!this.shareId || !this.prize) {
return this.$vux.alert.show({
title: "提示",
content: "订单不存在或者金额异常"
});
}
this.$router.replace({
path: "/payInfo",
query: {
orderId: this.shareId,
prize: this.prize,
userId: this.b_userId
}
});
},
mapInit() {
var self = this;
if (self.formD.latitude != "" && self.formD.longitude != "") {
var map = new qq.maps.Map(document.getElementById("showPosition"), {
center: new qq.maps.LatLng(39.916527, 116.397128),
zoom: 13,
panControl: false,
zoomControl: false,
scaleControl: false
});

var marker = new qq.maps.Marker({
position: new qq.maps.LatLng(
self.formD.latitude,
self.formD.longitude
),
map: map
});
var geocoder = new qq.maps.Geocoder({
complete: function(result) {
map.setCenter(result.detail.location);
self.address = result.detail.address;
}
});
geocoder.getAddress(
new qq.maps.LatLng(self.formD.latitude, self.formD.longitude)
);
}
},
close() {
this.supportDialog = false;
this.appDialog = false;
},
support() {
this.supportDialog = true;
},
goapp() {
this.appDialog = true;
},
showntousu() {
this.tousuDialog = true;
},
phone() {
this.pshown = true;
},
tousu() {
this.$vux.toast.text("投诉成功！", "top");
this.tousuDialog = false;
},
copy() {
var clipboard = new Clipboard("#copyBtn");
clipboard.on("success", e => {
this.$vux.toast.text("复制成功！", "top");
e.clearSelection();
});
},
showdialoge() {
this.name = "";
this.type = "";
this.tel = "";
},
hidedailog() {
this.shown = false;
this.pshown = false;
},
stop() {
var audio = document.getElementById("audio2");
if (audio == null) return;
audio.pause();
},
play(item) {
var audio = document.getElementById("audio2");
if (audio == null) return;
if (this.formD.autoPlayMusic) {
let data = JSON.parse(this.formD.music);
audio.src = data.path;
audio.pause();
audio.load();
audio.play();
}
},

query() {
if (!!this.form) {
this.formD = this.form;
this.ispreview = true;
this.$refs["bg-main2"].style.background = !this.formD.bgImage
? "#edea8f"
: `url(${this.formD.bgImage})`;
this.mapInit();
return;
}
var params = this.$route.query;
this.params = params;

if (!!params.shareId) {
this.shareId = params.shareId;
}

if (!!params.shown) {
this.shown = !!params.shown ? true : false;
}

if (!!params.code) {
this.$http
.get(
baseUrl.apiBaseUrl +
"api/wxpay/getUserInfo?code=" +
params.code +
"&state=" +
params.state
)
.then(({ data: res }) => {
if (res.code != "0000") {
console.log("获取用户信息失败");
} else {
this.user_id = res.result.data.user.userId;
this.userName =
res.result.data.user.username || res.result.data.user.nickname;
this.loading = true;
this.$http
.get(baseUrl.apiBaseUrl + "bargin/info/" + params.id)
.then(({ data }) => {
if (data.code == "0") {
this.formD = data.bargin;
this.$refs["bg-main2"].style.background = !this.formD
.bgImage
? "#edea8f"
: `url(${this.formD.bgImage})`;
this.mapInit();
var fore = data.bargin;
this.list = data.order;
document.title = fore.activityName;
var thumbnail = data.bargin.thumbnail;
var currentUrl = encodeURIComponent(
location.href.split("#")[0]
);
this.$http
.post(
baseUrl.apiBaseUrl +
"api/wxpay/initwxjs?url=" +
currentUrl
)
.then((data, status) => {
this.loading = false;
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: data.data.result.data.appId, // 必填，公众号的唯一标识
timestamp: data.data.result.data.timestamp, // 必填，生成签名的时间戳
nonceStr: data.data.result.data.nonceStr, // 必填，生成签名的随机串
signature: data.data.result.data.signature, // 必填，签名s
jsApiList: [
"onMenuShareTimeline",
"onMenuShareAppMessage",
"chooseWXPay",
"openLocation"
] // 必填，需要使用的JS接口列表
});

wx.ready(() => {
wx.error(function(res) {

console.log(res);
});

wx.checkJsApi({
jsApiList: [
"onMenuShareTimeline",
"onMenuShareAppMessage",
"chooseWXPay"
], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function(res) {



}
});

var shareParam = {
title: `我是${this.userName}, 参加了${
fore.activityName
}`, // 分享标题
desc: `${fore.activityName}, 联系电话: ${
fore.phone
}`, // 分享描述
link:
baseUrl.apiBaseUrl +
"dist/redirect.html?id=" +
params.id +
"&userid=" +
this.user_id +
"&shareId=" +
this.shareId +
"&hash=3pageInfo", // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
imgUrl: thumbnail, // 分享图标


trigger: function(res) {
console.log("用户点击发送给朋友");
},
success: function(res) {
console.log("已分享");
},
cancel: function(res) {
console.log("已取消");
},
fail: function(res) {}
};
wx.onMenuShareTimeline(shareParam);
wx.onMenuShareAppMessage(shareParam);
});
});
}
});
}
});
}
if (!!params.shareId) {
this.$http
.post(baseUrl.apiBaseUrl + "api/order/getOrderByOrderId", {
orderId: params.shareId
})
.then(({ data }) => {
if (data.code == "0000") {
this.prize = data.result.data.total_price;
this.b_userId = data.result.data.user_id;
}
});
this.$http
.post(baseUrl.apiBaseUrl + "bargin/queryBarginLog", {
orderId: params.shareId
})
.then(({ data }) => {
if (data.code == "0000") {
this.barginLogList = data.result.data;
}
});
}
}
}
};



