
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Range from 'vux/src/components/range/index.vue'

import AddImg from "./addImg.vue";
import ImgUpload from "./imgUpload.vue";
import page1Info from "./1pageInfo.vue";
import Data from "./data.js";
var baseUrl = require("../../config");
export default {
name: "page1-edit",
components: {
Group,
Cell,
CellBox,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
ImgUpload,
page1Info,
Range,
AddImg
},
data() {
return {
formD: Data,
params: {
id: ""
},
ispreview: false
};
},
computed: {},
mounted() {
let params = this.$route.query;
this.params = this.$route.query;
if (!!params.name) {
this.formD.page1Title = decodeURIComponent(params.name);
}
if (!this.$route.query.id) {
return;
}
const url = baseUrl.apiBaseUrl + "course/info/" + params.id;
this.$http.get(url).then(({ data }) => {
if (data.code == "0") {
this.formD = JSON.parse(data.course.courseContent);
if (data.course.courseContent != null) {
let activityDestription = JSON.parse(this.formD.activityDestription);
let sc = [];
activityDestription.forEach(e => {
var s =
"<img src='" + e.img + "' style='width: 100%; height: 100%;'/>";
sc.push({
type: e.type,
image: e.img,
html: s
});
});
this.$refs["activityDestription"].activeList = sc;
}
}
});
},
methods: {
editpage() {
this.ispreview = !this.ispreview;
},
preview() {
let activityDestription = [];
this.$refs["activityDestription"].activeList.forEach(item => {
activityDestription.push({
type: item.type,
img: item.image
});
});
this.formD.activityDestription = JSON.stringify(activityDestription);
this.ispreview = !this.ispreview;
},
save() {
let activityDestription = [];
this.$refs["activityDestription"].activeList.forEach(item => {
activityDestription.push({
type: item.type,
img: item.image
});
});
this.formD.activityDestription = JSON.stringify(activityDestription);
var a = JSON.stringify(this.formD);
this.$vux.loading.show({
text: "Loading"
});
this.$http
.post(baseUrl.apiBaseUrl + "course/save", {
courseContent: a,
courseType: 0,
id: this.params.id
})
.then(({ data }) => {
this.$vux.loading.hide();
if (data.code == "0") {
this.$vux.alert.show({
title: "提示",
content: "保存成功！",
onShow() {},
onHide() {}
});
} else {
this.$vux.alert.show({
title: "提示",
content: "保存失败！",
onShow() {},
onHide() {}
});
}
});
}
}
};



